#wrapper{background-color:#f3f4f8;width:100%;height:8vh;display:flex;justify-content:space-between;align-items:center}#wrapper .link{text-decoration:none;color:inherit}#wrapper .headerH1{font-family:'Lexend Mega';font-weight:lighter;font-size:2.7rem;position:relative;text-decoration:none;margin:0 6vw;color:#222f3e}#wrapper .headerH1 span{color:#546de5}#wrapper .button-link{background:none;border:none;font-size:1.25rem;font-family:'Poppins';font-weight:bold;color:#485460;cursor:pointer}#wrapper #nav_links{list-style:none;margin:0 2vw 0 0}#wrapper #nav_links li{display:inline-block;font-family:Poppins;font-weight:bold;font-size:1.25rem}#wrapper #nav_links li::after{display:block;content:'';-webkit-transform:scaleX(0);transform:scaleX(0);transition:-webkit-transform 250ms ease-in-out;transition:transform 250ms ease-in-out;transition:transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out}#wrapper #nav_links li:hover::after{-webkit-transform:scaleX(1);transform:scaleX(1);-webkit-transform-origin:0% 50%;transform-origin:0% 50%}#wrapper #nav_links #home{margin:0 6vw 0 0;color:#546de5}#wrapper #nav_links #home::after{border-bottom:solid 1.2px #546de5}#wrapper #nav_links #home #house{position:relative;top:0.2vh;padding-right:0.2vw}#wrapper #nav_links .rest{padding:0 1.5vw;color:#485460}#wrapper #nav_links .rest::after{border-bottom:solid 1.2px #485460}

